function toHex(value) {
  if (value !== 0 && !value) return '';
  var stringValue = value.toString(16);
  var s = '000000000' + stringValue;
  var bytes = 8;
  if (stringValue.length > 8) {
    s = '000000000000000000' + stringValue;
    bytes = 16;
  }

  return s.substr(s.length - bytes);
}

